@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@tailwind base;
@tailwind components;
body {
  @apply bg-purple-100 text-gray-700 flex flex-col min-h-screen;
}

@tailwind utilities;
input[type="number"],
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

h1 {
  @apply md:text-6xl text-3xl text-purple-900 font-bold my-6;
}

header {
  @apply shadow-lg mb-4;
}

header .nav-bar {
  @apply flex justify-between p-8;
}

header .nav-bar .menu-button {
  @apply md:h-12 md:w-12 h-8 w-8 hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

header .nav-bar .home-button {
  @apply font-logo md:text-6xl text-3xl hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

header .nav-bar .cart-button {
  @apply md:h-12 md:w-12 h-8 w-8 hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

main {
  @apply container mx-auto shadow-lg flex-grow mb-4 pb-8;
}

footer {
  @apply py-4 bg-white;
}

footer .footer-links {
  @apply flex justify-evenly items-center text-center my-4;
}

footer .footer-links a {
  @apply text-base font-light transform duration-200 hover:scale-110 scale-100;
}

footer .social-networks {
  @apply flex justify-evenly items-center;
}

footer .social-networks a {
  @apply transform duration-200 hover:scale-110 scale-100;
}

.teddy-card {
  @apply rounded-2xl shadow-md mt-8 max-w-xs bg-purple-200 h-72;
  @apply transform duration-200 hover:scale-110 scale-100;
}

.teddy-card:hover .teddy-card__price {
  @apply animate-bounce text-green-400;
}

.teddy-card__image {
  @apply shadow-md rounded-t-2xl h-3/4 w-full object-cover;
  @apply border-purple-300 border-b-2 border-solid;
}

.teddy-card__name {
  @apply px-4 font-medium text-lg pt-2;
}

.teddy-card__info {
  @apply px-4 flex justify-between items-center pb-2;
}

.teddy-card__description {
  @apply font-light;
}

.teddy-card__price {
  @apply font-bold text-lg text-green-500;
}

.product-list {
  @apply flex flex-wrap justify-start items-start;
}

.product-list li {
  @apply px-4;
}

.teddy {
  @apply grid md:grid-cols-2 md:grid-rows-1 grid-rows-2;
}

.teddy__image {
  @apply rounded-xl flex-shrink md:flex-shrink-0;
}

.teddy__info {
  @apply px-4;
}

.teddy__name {
  @apply font-semibold 2xl:text-2xl text-lg;
}

.teddy__description {
  @apply font-light 2xl:text-lg text-sm;
}

.teddy__color-label {
  @apply text-sm mr-4;
}

.teddy__color-select {
  @apply text-sm shadow-lg px-2 py-2 rounded-md border-purple-300 border-2 appearance-none w-36;
}

.teddy__color-select__container {
  @apply relative inline-block;
}

.teddy__color-select__arrow {
  @apply text-purple-900 absolute pointer-events-none;
  width: 1.5rem;
  right: 5%;
  top: 25%;
}

.teddy__color-select * {
  font-size: 1rem;
}

.teddy__shipping {
  @apply flex;
}

.teddy__shipping__image {
  @apply text-purple-900;
}

.teddy__shipping__text {
  @apply self-center pl-8;
}

.teddy__price {
  @apply font-bold text-green-700 text-right text-xl py-4;
}

.number-of-items {
  @apply w-full mb-4 py-2 px-4 rounded-lg text-center font-bold bg-purple-200 appearance-none;
}

.number-of-items__container {
  @apply w-3/4 relative mb-4;
}

.number-of-items__minus {
  @apply transform duration-200 hover:scale-125 scale-100 absolute;
  left: 5%;
  top: 20%;
}

.number-of-items__plus {
  @apply transform duration-200 hover:scale-125 scale-100 absolute;
  right: 5%;
  top: 20%;
}

.add-to-cart-button {
  @apply bg-green-700 text-white py-2 px-8 text-center rounded-lg transform duration-200 hover:scale-110 scale-100 font-bold;
}

.cart__item-list {
  @apply flex flex-col px-4;
}

.cart__item-list__title {
  @apply px-4 font-medium text-lg pt-2;
}

.cart-item {
  @apply flex justify-evenly my-4 flex-col md:flex-row;
}

.cart-item__info {
  @apply flex flex-row;
}

.cart-item__info__image {
  @apply w-1/3 md:w-1/4 object-cover;
}

.cart-item__info__text-container {
  @apply px-4 flex-grow;
}

.cart-item__info__name {
  @apply text-lg font-semibold;
}

.cart-item__info__color {
  @apply font-light;
}

.cart-item__info__price {
  @apply font-semibold text-right;
}

.cart-item__total-price {
  @apply text-lg font-semibold text-green-500 text-right;
}

@media (min-width: 768px) {
  .cart-item__total-price {
    min-width: 100px;
  }
}

.cart-item__buttons-container {
  @apply flex flex-wrap md:flex-nowrap justify-center items-center;
  min-width: 80px;
}

.cart-item__count {
  @apply w-full mb-4 py-2 px-4 rounded-lg text-center font-bold bg-purple-200 appearance-none;
  min-width: 80px;
}

.cart-item__count__container {
  @apply relative inline-block my-4 w-2/3;
}

.cart-item__count__minus {
  @apply transform duration-200 hover:scale-125 scale-100 absolute;
  left: 5%;
  top: 20%;
}

.cart-item__count__plus {
  @apply transform duration-200 hover:scale-125 scale-100 absolute;
  right: 5%;
  top: 20%;
}
